* {
    padding: 0%;
    margin: 0%;
    box-sizing: border-box;
}

.contanier {
    width: 100%;
    min-height: 100vh;
    background-color: rgb(181, 226, 244);
    display: flex;
    justify-content: center;
    align-items: center;
}

.imges {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-position: 10%;
}

.contanier-content {
    position: relative;
    z-index: 1;
    width: 80%;
    height: 500px;
    background-color: white;
    display: flex;
    border-radius: 20px;
    overflow: hidden;
}

.lift-box {
    width: 100%;
    height: 100%;
    flex: 1 1;
    background-color: rgb(255, 255, 255);
    padding: 20px 20px;
    display: flex;
    justify-content: center;
    /* align-items: center; */
}

.box {
    width: 100%;
    text-align: center;
}

.lift-box .input-box {
    width: 100%;
    margin-bottom: 0px;

}

.box .input-box input {
    width: 70%;
    padding: 10px 15px;
    font-size: 18px;
    border-radius: 30px;
    outline: none;
    border: none;
    text-transform: capitalize;
    background-color: rgb(229, 228, 228);
    margin-top: 15px;
}

.box .info {
    margin-top: 60px;
}

.box .info h1 {
    font-size: 3.6em;
    font-weight: 400;
    margin-bottom: 10px;
}

.box .t-type {
    font-size: 1.3em;
    margin-bottom: 30px;
}

hr {
    margin: 20px 80px;

}

.box .year {
    font-size: 20px;
    margin-bottom: 20px;
    font-weight: 400;
}

.box .time {
    font-size: 20px;
    margin-bottom: 10px;
    font-weight: 400;
}

.box .city .loction {
    font-size: 3.4rem;
    margin: 50px 10px;
    font-weight: 600;
    text-transform: capitalize;
}





.error {
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 80px;
}

.error-box {
    position: relative;
    top: -40px;
    width: 100%;
    height: 100%;
    background-color: rgb(100, 180, 241);
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: self-start;
    justify-content: center;
}

.error-box h1 {
    font-size: 3.6em;
    text-transform: capitalize;
    font-weight: 100;
    margin-bottom: 60px;
}


.error-box h3 {
    font-size: 1em;
    text-transform: capitalize;
    margin-bottom: 20px;
}




.right-box {
    padding: 10px;
    flex: 2 3;
    background-color: rgb(100, 180, 241);
}

.right-box .head {
    color: white;
    text-transform: capitalize;
    margin-left: 20px;
    font-weight: 500;
    font-size: 24px;
    margin-bottom: 8px;
}

.all-info-boxs {
    width: 100%;
    height: 90%;
    display: grid;
    grid-template-columns: repeat(3, auto);
    align-items: center;
    justify-content: center;
}


.all-info-boxs .bx {
    width: 200px;
    height: 120px;
    background-color: rgb(43, 43, 247);
    margin: 10px;
    border-radius: 10px;
    padding: 10px 20px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.bx h2 {
    color: white;
    font-size: 24px;
    font-weight: 400;
    text-transform: capitalize;
}

.bx p {
    color: white;
    font-size: 20px;
    font-weight: 200;
}




@media (max-width:1200px) {
    .all-info-boxs .bx {
        width: 180px;
        height: 120px;
    }

    .contanier-content {
        width: 90%;
        height: 500px;

    }

    .lift-box {
        width: 100%;
        height: 100%;
        flex: 1 3;
        padding: 10px 10px;

    }

}

@media (max-width:1050px) {
    .contanier {
        padding: 20px 0px;
    }

    .contanier-content {
        width: 95%;

        height: auto;
        background-color: white;
        display: flex;
        flex-direction: column;
        border-radius: 20px;
        overflow: hidden;
    }

    .box .city .loction {
        font-size: 4rem;
    }

    .all-info-boxs {
        position: relative;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        display: grid;
        grid-template-rows: repeat(2, auto);
    }

    .all-info-boxs .bx {
        min-width: 250px;

    }

    #box {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        width: 100%;
    }

    .date {
        margin-right: 20px;

    }

    .box .year {
        margin-bottom: 0px;
    }

    .box .time {
        margin-bottom: 00px;
    }
}

@media (max-width:900px) {
    .contanier {
        height: auto;
        /* padding-bottom: 220px; */
    }

    .contanier-content {
        width: 90%;
        height: auto;


    }

    .all-info-boxs {
        display: grid;
        grid-template-columns: repeat(2, auto);
        align-items: center;
        justify-content: center;
        padding: 10px;
    }

    .all-info-boxs .bx {
        min-width: 300px;
    }

    .box .input-box input {
        width: 90%;

    }

    .box .city .loction {
        font-size: 2.5rem;
    }

    .box .info h1 {
        font-size: 3em;

    }
}


@media (max-width:700px) {


    .all-info-boxs .bx {
        min-width: 250px;
    }
}

@media (max-width:600px) {

    .all-info-boxs .bx {
        min-width: 220px;
    }
}

@media (max-width:500px) {
    .all-info-boxs {
        display: flex;
    }

    .all-info-boxs .bx {
        min-width: 90%;
    }
}